@import "../../abstracts/_variables.scss";
@import "../../abstracts/_mixins.scss";


.nav {
  width: 530px;
  @include body-text();
  display: flex;
  align-items: center;
  line-height: 17px;
  color: $dark-50;
  text-transform: capitalize;
  justify-content: center;
  flex-shrink: 0;
    
  &__link {
    display: block;
    box-sizing: border-box;
    color: inherit;
    text-decoration: none;
    &::after {
      @include body-text();
      display: block;
      content: attr(title);
      height: 0px;
      overflow: hidden;
      visibility: hidden;
      font-weight: 700;
      line-height: 17px;
      color: $dark-50;
    }
    &:hover {
      font-weight: 700;
    }
  }

  &__item {
    text-align: center;
    margin-right: 27px;
    cursor: pointer;
    flex-shrink: 0;
    list-style: none;
    &:last-child {
      margin-right: 0px; 
    }
    &_selected {
      color: rgba(31, 32, 65, 0.75);
      font-family: $font;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
    }
  } 

  &__expanded {
    cursor: pointer;
    z-index: 3;
    &_content {
      display: none;
      padding: 10px 10px 10px 10px;
      margin-top: 25px;
      margin-left: 112px;
      position: absolute;
      border: 4px;
      border-radius: 4px;
      background-color: rgba(222,32,24,0.9);
      box-shadow: 0px 18px 18px rgba(0,0,0,0.2);
      z-index: 3;
    }
    &_element {
      color: white;
    }
  }
}
